.snapUpContainer{
       min-height: calc(100vh - 175px);
       overflow: auto;
       background-size: cover;
       background-repeat: no-repeat;
       background-position: center;
       width: 100%;
}
.onsite_preview_inner {
       position: relative;
       transform: scale(0.8);
       margin: -10%;
}

.subtree-container .t_overlay_{
       position: absolute;
}

.tb_onsite_upload_btn_wrap {
       padding: 10px
}

.tb_onsite_upload_btn,
.tb_onsite_upload_btn_wrap {
       align-items: center;
       display: flex;
       justify-content: center
}

.tb_onsite_upload_btn {
       flex-direction: column;
       max-width: 350px
}

.tb_onsite_btn {
       background-color: #000;
       border-radius: 3px;
       color: #fff;
       cursor: pointer;
       font-weight: 500;
       padding: 6px
}

.tb_onsite_btn,
.tb_onsite_qr_btn_wrap {
       align-items: stretch;
       display: flex;
       justify-content: center
}

.tb_onsite_qr_code {
       align-items: center;
       background: #fff;
       display: flex;
       justify-content: center;
       object-fit: cover;
       padding: 5px
}

.tb_onsite_upload_btn .btn-scan canvas {
       height: 100%;
       object-fit: cover;
       width: 100%
}

.tb_onsite_btn_txt {
       align-items: center;
       display: flex;
       font-size: 14px;
       font-weight: 400;
       padding: 0 10px
}

